@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Kdam+Thmor+Pro&display=swap');

.profile-pic{
  width:95px;
  transform:translateY(-30px);
  border-radius:10px;
  box-shadow: 0 0 20px rgba(0,0,0,0.4)
}
.profile-pic:hover{
  transform: translateY(-25px)scale(1.1);
  transition:0.3s
}
body{
  background:rgb(31, 30, 30);
}
*{
  color:white;
}
*, *::before, *::after{
  margin:0;
  padding:0;
  box-sizing:border-box
}
.FriendCard{
  background:#09101d;
  display:flex;
  justify-content:center;
  align-items: center;
  flex-direction:column;
  margin:25px;
  border-radius:5px;
  width:260px;
  height:340px;
  cursor:pointer;
  background: rgba( 255, 255, 255, 0.1 );
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
  backdrop-filter: blur( 4px );
  border-radius: 10px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );

}

.FriendCard:hover{
  transition:0.25s;
  box-shadow:0 0 30px rgba(0,0,255,0.4); 
   transform:scale(1.1); 
  border-radius:10px;
  border:2px solid rgba(255,255,255,0.35);
}
.container{
  display:flex;
  justify-content:center;
  align-items:center;
  flex-wrap:wrap;
  width:100vw;
  margin-top:80px;
}
.name{
  font-family:sans-serif;
  font-size:25px;
  text-align:center
}
.age{
  font-family:'Montserrat',sans-serif;
  font-weight:100;
  font-size:15px;
}
.grade{
  transform:translateY(54.4px);
  font-family:'Montserrat',sans-serif;
  font-weight:100;
  background:rgb(0, 46, 228);
  height:35px;
  width:260px;
  font-size:15px;
  border-radius:0 0 5px 5px;
  display:flex;
  justify-content: center;
  align-items:center;
  position:relative;
  top:3%;
  font-weight:bold;
}
.header{
  position:absolute;
  top:2%;
  font-family: 'Kdam Thmor Pro', sans-serif;
  font-size:45px;
}
.filterNameInput{
  position:absolute;
  top:5%;
  right:15%;
  z-index:5;
  width:260px;
  height:30px;
  color:black;
  outline:none;
  cursor:pointer;
  border:none;
  transition:0.3s;
}
.filterNameInput:focus{
  box-shadow:0 0 15px rgba(255,255,255,0.15),
  inset 0 0 20px rgba(0,0,0,0.25);
  border-radius:3px;
  padding:15px;
}
.star--favorite{
font-size:35px;

}
.SophomoreCard:hover{
  box-shadow: 0 0 30px rgba(228,68,0,0.45)
}
@media (max-width:1300px){
  .filterNameInput{
    width:240px;
    height:28px;
  }
  .header{
    font-size:40px;
  }
}
@media (max-width:1200px){
  .filterNameInput{
    width:225px;
    height:26px;
  }
  .header{
    font-size:37px;
  }
}
@media (max-width:1100px){
  .filterNameInput{
    width:210px;
    height:23px;
  }
  .header{
    font-size:34px;
  }
}
@media(max-width:1025px){
  .filterNameInput{
    width:195px;
    height:21px;
  }
  .header{
    font-size:31px;
  }
}
@media (max-width:1000px){
  .filterNameInput{
    top:12%;
    right:40%;
    width:200px;
    height:22px;
  }
}
@media (max-width:650px){
  .filterNameInput{
    right:38%;
  }
}
@media(max-width:560px){
  .filterNameInput{
    right:32%
  }
}